.Menu {
  margin: auto;
  padding: 10px;
}
.Btn {
  background-color: rgba(255, 255, 255, 0.1);
  color: #f7f1e3;
  cursor: pointer;
  border: none;
  height: 30px;
  margin-bottom: 1rem;
  min-width: 50px;
}
.delBtn {
  position: absolute;
  border-radius: 5px;
  background-color: red;
  cursor: pointer;
  margin: 10px;
  right: 10px;
  bottom: 0px;
}
.updateBtn {
  position: absolute;
  border-radius: 5px;
  background-color: green;
  cursor: pointer;
  margin: 10px;
  right: 60px;
  bottom: 0px;
}

.content {
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 20px;
  margin-left: 20px;
  padding: 20px;
  padding-right: 50px;
  padding-bottom: 40px;
  min-width: 400px;
  color: #f7f1e3;
  position: relative;
  margin-bottom: 2rem;

  line-height: 200%;
}

.open {
  animation-timing-function: linear;
  animation-duration: 1s;
  animation-name: slidein;
}
.close {
  background-color: white;
}
@keyframes slidein {
  from {
    margin-left: 100%;
    width: 200%;
  }

  to {
    width: 100%;
  }
}
