.AddForm {
  width: 500px;
  height: 100%;
  background-color: #dff9fb;
  margin: auto;
  text-align:center;
  
  margin-top:10px;
  margin-bottom:10rem;
  border-radius:10px 10px 10px 10px;

}
.Label{
  display:flex;
  align-items:center;
  justify-content:center;
  height:30px;
  border:none;
}
.Form {
  display: flex;
  flex-direction: column;
  border:none;
}
.Input{
  height:50px;
  border:none;
}
.Btn{
  border:none;
  height:30px;
  border-radius:0px 0px 10px 10px;
}
.Textarea {
  resize: none;
  height: 200px;
  border:none;
}

